@import "basic/variables";
@import "basic/mixins";

@import "basic/fonts";
@import "basic/reset";
// @import "basic/normalize";
@import "basic/basic";
// @import "basic/animations";
@import "blocks/header";


.container {
  padding-top: 50px;
  display: flex;
  justify-content: center;
}

.player-wrapper {
  // display: flex;
  // justify-content: flex-start;
  background-color: #cccccc;
  border-radius: 5px;
}
.player-track-controls {
  padding: 1em 1.5em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.player-track-name {
  background-color: rgba(255, 255, 255, 0.6);
  display: block;
  border-radius: 3px;
  padding: 0.3em 0.5em;
  cursor: pointer;
  width: 50%;
  white-space: nowrap;
  overflow: hidden;
  & > span {
    display: inline-block;
    animation: movingString 15s linear infinite;
    // width: 100%;
  }
}
@keyframes movingString {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.player-panel {
  width: 500px;  //test
  height: 60px; //test
  // background-color: #cccccc;
  // border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5em 1em;

}
.player-play {
  height: 100%;
  width: 50px;
  position: relative;
  display: block;
  border: 2px solid #808080;
  border-radius: 10px;
  cursor: pointer;
  background-color: transparent;
  &::before, &::after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    margin-top: -10px;
    background-color: #000000;
    height: 20px;
    width: 6px;
  }
  &::before {
    left: 50%;
    margin-left: -10px;
    // transform: translateX(-6px);
  }
  &::after {
    right: 50%;
    margin-right: -10px;
    // transform: translateX(6px);
  }
  &.stopped::before, &.stopped::after {
    background-color: transparent;

  }
  &.stopped::before {
    height: 0;
    border-left: 18px solid #000000;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
  }
  &.stopped::after {
    display: none;
  }
}

.player-slider {
  max-width: 60%;
  width: 100%;

}

.player-volume-panel {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 100px;
}
.player-mute {
  width: 32px;
  min-width: 32px;
  height: 32px;
  border: none;
  padding: 0;
  background-color: transparent;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  cursor: pointer;
  position: relative;
  &.active::after {
    content: '';
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -1px;
    top: 0;
    width: 2px;
    height: 100%;
    background-color: #000000;
    transform: rotate(45deg);
  }
}

.player-volume {
  width: 100%;
  margin: 8px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
.player-volume:focus {
  outline: none;
}
.player-volume::-webkit-slider-runnable-track {
  background: #928483;
  border: 2px solid #562425;
  border-radius: 3px;
  width: 100%;
  height: 7px;
  cursor: pointer;
}
.player-volume::-webkit-slider-thumb {
  margin-top: -10px;
  width: 10px;
  height: 23px;
  background: #6b1817;
  border: 2px solid #000000;
  border-radius: 8px;
  cursor: pointer;
  -webkit-appearance: none;
}
.player-volume:focus::-webkit-slider-runnable-track {
  background: #948786;
}
.player-volume::-moz-range-track {
  background: #928483;
  border: 2px solid #562425;
  border-radius: 3px;
  width: 100%;
  height: 7px;
  cursor: pointer;
}
.player-volume::-moz-range-thumb {
  width: 10px;
  height: 23px;
  background: #6b1817;
  border: 2px solid #000000;
  border-radius: 8px;
  cursor: pointer;
}
.player-volume::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 9px 0;
  color: transparent;
  width: 100%;
  height: 7px;
  cursor: pointer;
}
.player-volume::-ms-fill-lower {
  background: #908180;
  border: 2px solid #562425;
  border-radius: 6px;
}
.player-volume::-ms-fill-upper {
  background: #928483;
  border: 2px solid #562425;
  border-radius: 6px;
}
.player-volume::-ms-thumb {
  width: 10px;
  height: 23px;
  background: #6b1817;
  border: 2px solid #000000;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
.player-volume:focus::-ms-fill-lower {
  background: #928483;
}
.player-volume:focus::-ms-fill-upper {
  background: #948786;
}
/*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
how to remove the virtical space around the range input in IE*/
@supports (-ms-ime-align:auto) {
  /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
  .player-volume {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}



